import React, { Component } from 'react';
import { 
  BrowserRouter as Router,
  Link,
  Route,
  Switch
} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import "./style/global.scss";

// router
import ScrollToTop from "./components/ScrollToTop";

import {
  spring,
  AnimatedRoute,
  AnimatedSwitch 
} from 'react-router-transition';
// 
import Header from "./containers/Header";
import Home from "./containers/Home";
import Proportion from "./containers/Proportion";
import Complete from "./containers/Complete";
/*
<ul>
  <li>
    <Link to="/">Home</Link>
    <Link to="/proportion">persons</Link>
    <Link to="/complete">complete</Link>
  </li>
</ul>
*/
// we need to map the `scale` prop we define below
// to the transform style property
function mapStyles(styles) {
  return {
    opacity: styles.opacity,
    transform: `scale(${styles.scale})`,
  };
}

// wrap the `spring` helper to use a bouncy config
function bounce(val) {
  return spring(val, {
    stiffness: 330,
    damping: 22,
  });
}


class App extends Component {
  baseName = (() => {
    if (process.env.NODE_ENV === 'development') return  '/';
    if (process.env.NODE_ENV === 'production') return '/coffee-store';
    
  })()
  componentDidMount() {
    console.log('process.env', process.env.NODE_ENV);
    
    
  }
  render() {
    return (
      <Router basename={this.baseName}>
        <ScrollToTop>
          <div className="App">
            <Header />
            
            <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/proportion" component={Proportion} />
              <Route path="/complete" component={Complete} />
            </Switch>
            
          </div>
        </ScrollToTop>
      </Router>
    );
  }
}
/**
 * 
 * 
 */
export default App;



